import React from 'react';
import styled from "styled-components";
import Zoom from 'react-reveal/Zoom';
import Fade from 'react-reveal/Fade';
import '../routes/Home.css';
import { useScrollFadeIn } from '../hooks';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faPaperPlane } from '@fortawesome/free-solid-svg-icons'

const Container = styled.div`
  margin:0 auto;
  text-align:center;
  margin-bottom:200px;
`;

const TitleContainer = styled.div`
  z-index:1;
  top:30px;
  left:-30px;
`;
const Title = styled.span`
  font-size:120px;
  font-weight:900;
  color:white;
  opacity:0.6;

  @media(max-width:700px){
    font-size:70px;
  }
`;
const ProfileImg = styled.img`
  width:30%;
  border-radius:10px;
  @media (max-width:700px){
    width:40%;
  }
`;

const Card = styled.div`
  width:70%;
  display:inline-block;
  border-radius:20px;
  box-shadow:0px 0px 10px 10px rgba(0,0,0,0.05);
  padding:5px;
  padding-top:50px;
  background-color:white;
  text-align:center;
  z-index:2;
  margin-top:20px;
  @media (max-width:700px){
    width:80%;
  }
`;

const TextDiv = styled.div`
  margin:20px;
`;
const Name = styled.p`
  font-size:25px;
  font-weight:700;
  @media(max-width:700px){
    font-size:20px;
  }
`;
const TextTitle = styled.p`
  margin-top:20px;
  font-size:18px;
  @media(max-width:700px){
    margin-top:16px;
    font-size:16px;
  }
`;
const Text = styled.p`
  margin-top:16px;
  margin-bottom:20px;
  font-size:18px;
  @media(max-width:700px){
    margin-top:13px;
    font-size:16px;
  }
`;
const Greet = styled.div`
  margin:0 auto;
  max-width:700px;
  margin-bottom:7px;
  line-height:23px;
  text-align: justify;
  font-weight:100;
  @media(max-width:700px){
    font-weight:100;
    font-size:13.5px;
  }
`;
