const Container = styled.div`
  margin:0 auto;
  text-align:center;
  margin-bottom:100px;
`;

const TitleContainer = styled.div`
  z-index:1;
  top:30px;
  left:-30px;
`;
const Title = styled.span`
  font-size:120px;
  font-weight:900;
  color:white;
  opacity:0.9;

  @media(max-width:700px){
    font-size:60px;
  }
`;

const Card = styled.div`
  width:950px;
  display:inline-block;
  margin:0 auto;
  border-radius:20px;
  z-index:2;
  margin-top:20px;
  @media (max-width:700px){
    width:95%;
  }
`;

const Cards = styled.div`
  text-align:left;
  border-radius:10px;
  background-color: rgba( 0,0,0, 0.7 );
  color:white;
  width:95%;
  height:200px;
  padding:20px;
  @media (max-width:700px){
    padding:10px;
    height:100%;
  }
`;

const CardsTitle = styled.p`
  margin:10px;
  font-size:27px;
  @media (max-width:700px){
    font-size:19px;
  }
`;

const CardsSubTitle = styled.p`
  margin:10px;
  font-weight:100;
  font-size:18px;
  @media (max-width:700px){
    font-size:15px;
  }
`;

